*,
*::after,
*::before {
	box-sizing: border-box;
}

:root {
	font-size: 1.3rem;
}


/* From styles/font.scss */
@font-face { 
  font-family: 'Saol Display';
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src:
    url('../assets/fonts/SaolDisplay-Regular.woff2') format('woff2'),
    url('../assets/fonts/SaolDisplay-Regular.woff') format('woff');
}

body {
	margin: 0;
	--color-text: #3e3f3f;
	--color-bg: #f7f5f4;
	--color-link: red;
	--color-link-hover: #000;
	--color-deco: white;
	color: var(--color-text);
	background-color: var(--color-bg);
	font-family: 'Montserrat', sans-serif;
}


a {
	text-decoration: none;
	color:#3e3f3f;
}



a:hover,
a:focus {
	color: red;
	outline: none;
}


.frame {
	padding: 2.5rem 3rem;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 10;
}

.frame__title {
	color: #3e3f3f;
	margin: 0 0 2.5rem;
}

h1 {
	font-size: 1.8rem;
}

@media only screen and (max-width: 1023px) {
	.frame {
		position: absolute;
		top: auto;
		left: auto;
		padding: 1rem 1rem;
		display: flex;
		justfiy-content: flex-start;
		align-items: center;
		width: 100%;
		background: rgba(255,255,255,1);
	}
	.frame__title-wrap {
		display: flex;
		order: 2;
		position: relative;
		height: 100%;
	}
	.frame__title-wrap::before {
		content: "";
		display: block;
		width: 1px;
		height: 50%;
		background: rgba(0,0,0,0.2);
		position: absolute;
		top: 25%;
		left: 1rem;
	}
	.frame__title {
		display: flex;
		margin: 0;
		padding: 0 0 0 2rem;
		order: 1;
	}
	.frame__title {
		margin: 0;
	}

}

.frame__links .iconify {
		margin: -0.3rem 0 -0.15rem;
	}

.frame__links {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.frame a {
	margin: 0.25rem 0;
	outline: none;
	font-size: 20px;
	letter-spacing: 1px;
	text-decoration: none;
}

.wrapperContent {
	position: relative;
	display: block;
	padding: 2.5rem 0 2.5rem;
}

section {
	position: relative;
	display: block;
	max-width: 1080px; 
	margin: 0 auto;
}



.content {
	position: relative;
	display: block;
}

.content ul {
	position: relative;
	display: block;
	padding: 0;
	margin: 0;
}


.content p ul {
	list-style-type:circle;
	display: list-item;
}

.content p ul li{
	display: list-item;
}

.content ul li {
	position: relative;
	display: block; 
	/*padding: 0 0 2.5rem;*/
	margin: 0;
}

.content ul li img {
	display: block;
	width: 100%;
	height: auto; 
	/*padding: 0 0 2.5rem;*/
}

.content h2 {
	position: relative;
	display: block;
	/*padding: 3rem 0 1rem;*/
	font-size: 3.375rem;
	font-weight: 300;
}

.content h3 {
	position: relative;
	display: block;
	/*padding: 3rem 0 1rem;*/
	font-size: 2.5rem;
	font-weight: 300;
}

.content h4 {
	position: relative;
	display: block;
	/*padding: 3rem 0 1rem;*/
	padding: 1rem 0 0 0;
	font-size: 2.5rem;
	font-weight: 300;
}

.content p{
	padding: 0 0 0.5rem;
	color: #888;
}

.fbFrame {
	position: relative;
	display: block;
	width: 75%;
	margin: 0 auto;
	padding: 0 0 3.5rem;
	text-align: center;
}

.nopad{
	padding: 0 !important;
}

.content p a {
	color: #666;
	text-decoration: underline;
}
.content p a:hover {
	color: red;
	text-decoration: underline;
}


.caption {
	position: relative;
	display: block;
	width: 75%; /*80%*/
	margin: 0 auto;
	/*padding: 0 0 3rem 0;*/
	padding: 3.5rem 0 3.5rem 0;
}

.imgsRow{
	padding-top: 2rem;
}

ul.specialUl {
	display:list-item;
	margin-left: 1rem;
	color: #888;
}
ul.specialUl li {
	display: list-item;
}

.padBufferTop {
	padding-top: 1rem;
}

.imgPadding{
	padding-bottom: 4rem;
}

@media only screen and (max-width: 1680px) {
	.wrapperContent {
		padding: 8rem 0 2.5rem;
	}

}

@media only screen and (max-width: 1023px) {
	
	.imgsRow{
	padding-top: 0;
	}
	

	.wrapperContent {
		padding: 6rem 0 2.5rem;
	}
	section {
		max-width: 96%;
	}
	.caption {
		width: 90%;
		padding: 2rem 0 2rem 0;
	}

	.fbFrame {
		width: 90%;
		padding: 0 0 2rem 0;
		overflow: hidden;
	}

	.content h2 {
		font-size: 2rem;
	}
	.content h3 {
		font-size: 1.4rem;
	}

	.content h4 {
		font-size: 1.4rem;
		padding: 0.5rem 0 0 0;
	}
}

/* Scrolling effect */

html,
body {
  height: 100%;
}

[data-scroll-content] {
  padding: 10vw 0;
}

figure {
  padding: 10vw 5vw;

  img {
    display: block;
    width: 100%;
    height: auto;
  }
}



/* Contact Form CSS - formspree */

.form {
	padding: 4rem; 
}

@media only screen and (max-width: 1023px) {
	.form {
	padding: 0 2rem;
	}
}

/* reset */
form input,
form select,
form textarea,
form fieldset,
form optgroup,
form label,
.StripeElement {
  font-family: inherit;
  font-size: 100%;
  color: inherit;
  border: none;
  border-radius: 0;
  display: block;
  width: 100%;
  padding: 0;
  margin: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
}
form label,
form legend {
  font-size: 0.825rem;
  margin-bottom: 0.5rem;
}
/* border, padding, margin, width */
form input,
form select,
form textarea,
.StripeElement {
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.2);
  background-color: rgba(255, 255, 255, 0.9);
  padding: 0.75em 1rem;
  margin-bottom: 1.5rem;
}
form input:focus,
form select:focus,
form textarea:focus,
.StripeElement:focus {
  background-color: white;
  outline-style: solid;
  outline-width: thin;
  outline-color: gray;
  outline-offset: -1px;
}
form [type="text"],
form [type="email"],
.StripeElement {
  width: 100%;
}
form [type="button"],
form [type="submit"],
form [type="reset"] {
  width: auto;
  cursor: pointer;
  -webkit-appearance: button;
  -moz-appearance: button;
  appearance: button;
}
form [type="button"]:focus,
form [type="submit"]:focus,
form [type="reset"]:focus {
  outline: none;
}

form select {
  text-transform: none;
}

